.post__meta {
  font-size: $meta-font-size;
  color: $meta-color;
  font-weight: $meta-font-weight;
  margin-top:-1rem;
  padding-bottom: 0.25rem;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    display: block;
    width: 100%;
    height: 2px;
    background: $pink;
    background: linear-gradient(to right, rgba($pink, 1) 0%, rgba($blue, 1) 50%, rgba($cyan, 1) 100%);
  }
  
}
.post h2 {
  color: $one;
  font-weight: 500;
}
.post h3 {
  color: $one-yellow;
}
